'use client'

import { useMemo, useState, useEffect } from 'react'
import { usePathname, useRouter, useSearchParams } from 'next/navigation'
import { Header } from '@/components/header'
import { Footer } from '@/components/footer'
import { ProductCard } from '@/components/product-card'
import { products } from '@/lib/products'

type SortOption = 'featured' | 'price-low' | 'price-high' | 'newest' | 'rating'

interface CategoryPageTemplateProps {
  title: string
  subtitle: string
  categoryName?: string
  filterBy?: (product: (typeof products)[number]) => boolean
  subcategoryFilters: string[]
  showSeasonFilter?: boolean
  seasonFilters?: Array<'All-Season' | 'Winter' | 'Festive'>
}

const defaultSeasonFilters: Array<'All-Season' | 'Winter'> = ['All-Season', 'Winter']

const slugify = (value: string) =>
  value
    .toLowerCase()
    .replace(/&/g, 'and')
    .replace(/[^a-z0-9]+/g, '-')
    .replace(/(^-|-$)+/g, '')

export function CategoryPageTemplate({
  title,
  subtitle,
  categoryName,
  filterBy,
  subcategoryFilters,
  showSeasonFilter = false,
  seasonFilters = defaultSeasonFilters,
}: CategoryPageTemplateProps) {
  const [selectedSubcategory, setSelectedSubcategory] = useState<string | null>(null)
  const [season, setSeason] = useState<string>(seasonFilters[0] || 'All-Season')
  const [sortBy, setSortBy] = useState<SortOption>('featured')
  const router = useRouter()
  const pathname = usePathname()
  const searchParams = useSearchParams()

  useEffect(() => {
    const querySubcategory = searchParams.get('subcategory')
    if (!querySubcategory) {
      setSelectedSubcategory(null)
      return
    }
    const matched = subcategoryFilters.find(
      (label) => slugify(label) === querySubcategory
    )
    setSelectedSubcategory(matched || null)
  }, [searchParams, subcategoryFilters])

  const handleSubcategoryChange = (label: string | null) => {
    setSelectedSubcategory(label)
    const params = new URLSearchParams(searchParams.toString())
    if (label) {
      params.set('subcategory', slugify(label))
    } else {
      params.delete('subcategory')
    }
    const queryString = params.toString()
    router.replace(queryString ? `${pathname}?${queryString}` : pathname, { scroll: false })
  }

  const matchingProducts = useMemo(() => {
    return products.filter((product) => {
      if (filterBy) {
        return filterBy(product)
      }
      if (categoryName) {
        return product.category === categoryName
      }
      return true
    })
  }, [categoryName, filterBy, products])

  const filteredAndSortedProducts = useMemo(() => {
    let filtered = matchingProducts

    if (selectedSubcategory) {
      filtered = filtered.filter(
        (product) => (product.subcategory || '').toLowerCase() === selectedSubcategory.toLowerCase()
      )
    }

    if (showSeasonFilter && season && season !== 'All-Season') {
      filtered = filtered.filter((product) => {
        const productSeason = product.season || 'All-Season'
        if (productSeason === season) {
          return true
        }
        if (season === 'Winter') {
          return productSeason === 'All-Season'
        }
        if (season === 'Festive') {
          return productSeason === 'Festive'
        }
        return false
      })
    }

    switch (sortBy) {
      case 'price-low':
        filtered = [...filtered].sort((a, b) => a.price - b.price)
        break
      case 'price-high':
        filtered = [...filtered].sort((a, b) => b.price - a.price)
        break
      case 'rating':
        filtered = [...filtered].sort((a, b) => b.rating - a.rating)
        break
      case 'newest':
        filtered = [...filtered].sort((a, b) => b.id - a.id)
        break
      default:
        filtered = [...filtered]
    }

    return filtered
  }, [matchingProducts, season, selectedSubcategory, showSeasonFilter, sortBy])

  return (
    <>
      <Header />
      <main className="min-h-screen bg-background">
        <div className="bg-muted/50 px-4 md:px-6 py-8">
          <div className="max-w-7xl mx-auto">
            <h1 className="text-3xl md:text-4xl font-bold mb-2">{title}</h1>
            <p className="text-muted-foreground">{subtitle}</p>
          </div>
        </div>

        <div className="max-w-7xl mx-auto px-4 md:px-6 py-12">
          <div className="mb-8 space-y-4">
            <div className="flex flex-col md:flex-row gap-4">
              <div className="flex flex-wrap gap-2">
                <button
                  onClick={() => handleSubcategoryChange(null)}
                  className={`px-4 py-2 rounded-full smooth-transition ${
                    selectedSubcategory === null
                      ? 'bg-primary text-primary-foreground'
                      : 'bg-muted hover:bg-muted/80 text-foreground'
                  }`}
                >
                  All
                </button>
                {subcategoryFilters.map((cat) => (
                  <button
                    key={cat}
                    onClick={() => handleSubcategoryChange(cat)}
                    className={`px-4 py-2 rounded-full smooth-transition ${
                      selectedSubcategory === cat
                        ? 'bg-primary text-primary-foreground'
                        : 'bg-muted hover:bg-muted/80 text-foreground'
                    }`}
                  >
                    {cat}
                  </button>
                ))}
              </div>

              {showSeasonFilter && (
                <div className="flex gap-2">
                  {seasonFilters.map((label) => (
                    <button
                      key={label}
                      onClick={() => setSeason(label)}
                      className={`px-4 py-2 rounded-full smooth-transition ${
                        season === label
                          ? 'bg-primary text-primary-foreground'
                          : 'bg-muted hover:bg-muted/80 text-foreground'
                      }`}
                    >
                      {label === 'All-Season' ? 'All Season' : label}
                    </button>
                  ))}
                </div>
              )}

              <div className="md:ml-auto">
                <select
                  value={sortBy}
                  onChange={(e) => setSortBy(e.target.value as SortOption)}
                  className="px-4 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                >
                  <option value="featured">Featured</option>
                  <option value="newest">Newest</option>
                  <option value="price-low">Price: Low to High</option>
                  <option value="price-high">Price: High to Low</option>
                  <option value="rating">Top Rated</option>
                </select>
              </div>
            </div>

            <div className="text-sm text-muted-foreground">
              Showing {filteredAndSortedProducts.length} products
            </div>
          </div>

          {filteredAndSortedProducts.length === 0 ? (
            <div className="text-center py-12">
              <p className="text-lg text-muted-foreground">No products found</p>
            </div>
          ) : (
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
              {filteredAndSortedProducts.map((product) => (
                <ProductCard key={product.id} {...product} />
              ))}
            </div>
          )}
        </div>
      </main>
      <Footer />
    </>
  )
}





